import { useEffect, useState } from 'react'


const Allrise=()=>{


    const [users ,setUsers]=useState([])


    const getUsers= async ()=>{
            const res= await fetch('https://api.tvmaze.com/search/shows?q=all')
            setUsers(await res.json());
    }

    useEffect(()=>{
        getUsers();
    },[]);
    
    return(
        <>
            <h2 className="fisth2">Shows List With All The Detail</h2>
            <div className="container-fluid mt-3" >
                <div className="row text-center">
                    {
                        users.map((curEle)=>{
                            const links='{curEle.show.officialSite}'
                            return(
                            <div className="col-10 col-md-4 mt-5" key={curEle.show.id}> 
                                <div className="card1 p-2">
                                    <div className="d-flex align-item-center" key={curEle.show.id}>
                                        <div className="ml-3 w-100">
                                            <h4 className="mb-0 mt-0 textLeft">{curEle.show.name}</h4>
                                            <div className="p-2 mt-2 bg-primary d-flex justify-content-between rounded text-white stats">
                                                <div className="d-flex flex-column"> <span className="articles">About</span> <span className="number1">{curEle.show.summary}</span> </div>
                                            </div>
                                            <div className="d-flex flex-column"><span className="detail1">More Detail</span> <span className="number4"><a href={curEle.show.url}>{curEle.show.url}</a></span></div>
                                            <div className="d-flex flex-column"><span className="detail1">Go Back</span> <span className="number4"><a href="/">Back</a></span></div>
                                        </div>
                                    </div> 
        
                                </div>
                            </div>

                            )
                        })
                    }

                </div>

            </div>
        </>
    )
}

export default Allrise;