import  { useEffect, useState } from 'react'


const UseEffectAPI=()=>{


    const [users ,setUsers]=useState([])


    const getUsers= async ()=>{
            const res= await fetch('https://api.tvmaze.com/search/shows?q=all')
            setUsers(await res.json());
    }

    useEffect(()=>{
        getUsers();
    },[]);

    return(
        <>  

            <div className="firsth2"><h2>Shows List With Name</h2></div>
            <div className="container-fluid mt-3" >
                <div className="row text-center">
                    {
                        users.map((curEle)=>{
                            return(
                            <div className="col-10 col-md-4 mt-5" key={curEle.show.id}> 
                                <div className="card p-2">
                                    <div className="d-flex align-item-center" key={curEle.show.id}>
                                        <div className="image" > <img src='/*{curEle.show.image.medium}*/' alt="null" className="rounded" width="500"/></div>
                                        <div className="ml-3 w-100">
                                            <h4 className="mb-0 mt-0 textLeft">{curEle.show.name}</h4>
                                            <div className="p-2 mt-2 bg-primary d-flex justify-content-between rounded text-white stats">
                                                <div className="d-flex flex-column"> <span className="articles">Type</span> <span className="number1">{curEle.show.type}</span> </div>
                                                <div className="d-flex flex-column"><span className="followers">Language</span> <span className="number2">{curEle.show.language}</span> </div>
                                                <div className="d-flex flex-column"><span className="rating">Rating</span> <span className="number3" >{curEle.show.rating.average}</span> </div>
                                                <div className="d-flex flex-column"><span className="detail">All Detail</span> <span className="number4"><a href="/allrise">Click here</a></span></div>
                                            </div>
                                        </div>
                                    </div> 
        
                                </div>
                            </div>

                            )
                        })
                    }

                </div>

            </div>
        </>
    )
}

export default UseEffectAPI;