import './App.css';
import UseEffectAPI from './components/useEffectAPI'
import {Route,Switch}from 'react-router-dom';
import Allrise from './components/allrise';

function App() {
  return (
    <div className="App">
        <Switch>
          <Route exact path="/" component={UseEffectAPI}/>
          <Route exact path="/allrise" component={Allrise}/>
        </Switch>
    </div>
  );
}

export default App;
